import { css } from '@emotion/react';

import { PreventLargeWidthBox, Stack } from 'src/components/layout';
import { BestFitProductQuestionPaper } from 'src/components/papers';
import useProductInfo from 'src/hooks/useProductInfo';
import useSurveysForBestFit from 'src/hooks/useSurveysForBestFit';

// scroll-snap-points-y: repeat(100vh);
const scrollSnapTypeMandatory = css`
  overflow: hidden;
  scroll-snap-type: x mandatory;
`;

const BestFitProductSurvey = (): JSX.Element => {
  const { name: brandName } = useProductInfo();
  const { surveys } = useSurveysForBestFit({
    brandName,
  });

  return (
    <PreventLargeWidthBox>
      <Stack
        direction="row"
        justifyContent="space-between"
        css={scrollSnapTypeMandatory}
      >
        {surveys.map(({ question }, index) => {
          const key = `${index}-${question}`;
          return <BestFitProductQuestionPaper key={key} index={index} />;
        })}
      </Stack>
    </PreventLargeWidthBox>
  );
};

export default BestFitProductSurvey;
